"use client";

import { useState } from "react";
import { useWodComments } from "./WodCommentsStore";

export default function WodComments({ date }: { date: string }) {
  const { commentsByDate, addComment } = useWodComments();
  const [text, setText] = useState("");

  const comments = commentsByDate[date] ?? [];

  return (
    <section className="rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
      <div className="text-sm font-semibold">
        Comentarios ({comments.length})
      </div>

      <div className="mt-2 space-y-2">
        {comments.map((c) => (
          <div
            key={c.id}
            className="rounded-lg bg-gray-50 px-3 py-2 text-sm"
          >
            {c.text}
          </div>
        ))}

        {comments.length === 0 && (
          <div className="text-sm text-gray-500">
            S√© el primero en comentar üí¨
          </div>
        )}
      </div>

      <form
        className="mt-3 flex gap-2"
        onSubmit={(e) => {
          e.preventDefault();
          if (!text.trim()) return;
          addComment(date, text.trim());
          setText("");
        }}
      >
        <input
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Escrib√≠ un comentario‚Ä¶"
          className="flex-1 rounded-xl border border-gray-300 px-3 py-2 text-sm"
        />
        <button className="rounded-xl bg-gray-900 px-4 py-2 text-sm text-white">
          Enviar
        </button>
      </form>
    </section>
  );
}
